  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color: #f1f1f1;
}
/* Skip Content */
skip-to-main a{
  font-size: 1.2rem;
  font-weight: 800;
}
.skip-link-main{
  text-decoration: none;
  position: absolute;
  background: #eeeeee;
  border-radius: 5px;
  color: #111111;
  padding: 10px 20px;
  top: -60px;
  left: 40px;
  z-index: 300;
  transition: all 0.4s ease-in-out;
  
}
.skip-link-main:focus {
  top: 12px;
  scroll-behavior: smooth;
}


 /* header content*/

header{
  display: flex;
  background-color: #201f1f;
  padding: 5px 0;
  justify-content: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 200;
}
app-bar{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  padding: 0.7rem 0;
}
nav{
  display: flex;
  position: sticky;
  align-items: center;
}
nav ul{
  display: flex;
  gap: 60px;
}
nav li{
  list-style-type: none;
}
nav li a{
  font-family: Arial, Helvetica, sans-serif;
  color: rgb(231, 185, 84);
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  padding: 12px 0;
}
nav li:hover{
  transform: scale(1.1);
}
nav li a:hover {
  color: #eeeeee;
}
.logo{
  display: flex;
  text-decoration: none;
}
.logo-name{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: rgb(231, 185, 84);
  font-weight: 600;
  padding: 10px 0;
  font-size: 2rem;
}
.logo:hover{
  transform: scale(1.1);
}
.logo-name:hover{
  color: #eeeeee;
}
.hamburger {
  font-size: 3.2rem;
  text-decoration: none;
  color: rgb(231, 185, 84);
  display: none;
  margin-left: auto;
}
button{
  border: none;
  cursor: pointer;
  background-color: transparent;
}


/* hero element */

.hero-restaurant{
  display: flex;
  align-items: center;
  text-align: center;
  min-height: 500px;
  width: 100%;
  background-image:linear-gradient(rgba(78, 57, 57, 0.7),rgba(4,9,30,0.7)), url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
}
.hero-desc-restaurant{
  margin: 0 auto;
  max-width: 850px;
}
.hero-title{
  color: rgb(255, 253, 253);
  font-size: 2rem;
  padding: 10px;
  font-weight: 700;
  letter-spacing: 0.0155em;
}
.hero-desc{
  color: rgb(233, 226, 226);
  font-size: 1rem;
  font-weight: 300;
  line-height: 2.5;
  padding: 3px 5px;
}
.hero-restaurant button{
  font-size: 1.3rem;
  border: 2px solid white;
  color: white;
  padding: 10px 15px;
  width: 300px;
  margin-top: 10px;
  transition: all 0.15s ease-in-out;
  border-radius: 7px;
}
.hero-restaurant button:hover{
  background-color: #fff;
  color: #000;
}

.hero-restaurant button:focus {
  background-color: #fff;
  color: #000;
}


/* Container Content */

.content-restaurants{
  width: 100%;
  text-align: center;
  margin: 60px auto;
}
.content-label{
  text-align: center;
  font-size: 3rem;
  font-weight: bold;
  color: rgb(172, 139, 68);
  margin-bottom: 2vh;
}

/* list card */
.lists{
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); */
  grid-template-columns: repeat(3, 1fr);
  text-align: left;
  gap: 25px;
  align-items: start;
  margin: 50px 50px;
}
#lists{
  margin-top: 3vh !important;
}

.restaurants-item{
  width: 100%;
  border-radius: 7px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(17, 14, 14, 0.452);
}


/* Restaurant Card */

.location{
  font-size: 1.25rem;
  padding: 10px 7px;
  border-radius: 3px;
  display: flex;
  font-weight: bold;
  margin-top: 15px;
  position: absolute;
  color: #fafafa;
  z-index: 1;
  background-color: rgb(231, 185, 84);
}
.restaurant-item_image{
  object-fit: cover;
  height: 200px;
  z-index: 0;
  width: 100%;
}
.restaurant-item_image:hover {
  transform: scale(1.1);
  transition: transform 0.7s ease-in-out;
}
.restaurant-item-description {
  color: #141313;
  max-height: 100px; 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  display: -webkit-box;
  -webkit-line-clamp: 4; 
  -webkit-box-orient: vertical;
  font-size: 1.25rem;
  margin: 10px 0;
}
.restaurant-item-date{
  font-size: 1.5rem;
  font-weight: 500;
  letter-spacing: 0.025em;
}

.restaurant-item-date span{
  font-size: 1.25rem;
  font-weight: 400;
}
.restaurant-name{
  margin-top: 15px;
}
.restaurant-name a{
  text-decoration: none;
  color: rgb(231, 185, 84);
  font-size: 1.75rem;
  padding: 10px 0;
}
.restaurant-name a:hover{
  color: #141313;
}
.restaurant-item-content{
  padding: 17px;
}

/* like button */
/* .restaurant-img-detail .button {
  width: 100%;
} */

.restaurant-img-detail .like-button {
  display: flex;
  justify-content: right;
  align-items: right;
  
}

.restaurant-img-detail .like {
  position: absolute;
  z-index: 4;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;
  margin-top: 8px;
  margin-right: 10px;
}

.restaurant-img-detail .like:focus i {
  border: 2px solid red;
}

.red {
  color: red;
}

.restaurant-img-detail .like i {
  font-size: 20px;
  font-weight: bold;
  background: rgba(255, 255, 255);
  padding: 2px;
  height: 45px;
  width: 45px;
  border-radius: 50%;
  margin-top: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

/* detail card */

.detail-container{
  padding: 100px 120px 100px 100px;
  margin-top: 40px;
  max-width: 1300px;
  width: 100%;
  margin: auto;
}
.detail{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-bottom: 30px;
}
.restaurant-img-detail{
  
  width: 100%;
  max-height: 800px;
}
.restaurant-img-detail img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  align-items: center;
  position: relative;
}
.restaurant-detail {
  padding: 15px 0;
}
.restaurant-detail .restaurant-name-detail{
  font-size: 40px;
  font-weight: bold;
  padding: 15px 0px;
  color: rgb(231, 185, 84);
}
.restaurant-detail p{
  font-size: 24px;
  padding: 5px 0;
  font-weight: 700;
}
.restaurant-detail p span, .restaurant-detail span{
  font-weight: 200;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
}
.restaurant-desc .menu{
  font-size: 1.5rem;
  font-weight: bold;
  padding: 15px 0px;
}
.restaurant-desc p{
  font-weight: 200;
  font-size: 1.4rem;
  font-family: Arial, Helvetica, sans-serif;
}

 /* detail review */
 .review {
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  margin-bottom: 30px;
}

.review-card {
  margin-top: 3vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2vh;
}

.item-review {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
  padding: 17px;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
}

.item-reviewer {
  display: flex;
}

.reviewer-list {
  text-align: left;
  font-size: 1rem;
  font-family: Arial, Helvetica, sans-serif;
}

.reviewer-list p:first-child {
  font-weight: bold;
  color: rgb(231, 185, 84);
}

.description-review {
  font-size: 14px;
  text-align: justify;
  margin-top: 10px;
  font-family: Arial, Helvetica, sans-serif;
}

.add-review {
  display: flex;
  flex-direction: column;
  max-width: 100%;
}
.add-review h2{
  margin: 20px 0;
}
.add-review h2, label{
  font-family: Arial, Helvetica, sans-serif;
}


.add-review input,
textarea {
  font-family: Arial, Helvetica, sans-serif;
  width: 30%;
  margin-top: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  font-size: 14px;
}

.add-review input {
  height: 50px;
  margin-bottom: 24px;
  max-width: 20%;
}

.add-review textarea {
  max-width: 50%;
}

.add-review textarea:focus,
.add-review input:focus {
  outline: none;
  border: 2px solid #717c86;
}

.add-review .submit {
  width: 50%;
  display: flex;
  justify-content: left;
}

.submit button {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  margin-top: 20px;
  padding: 14px 30px;
  border-radius: 5px;
  border: none;
  background-color: #85929E;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.submit button:hover,
.submit button:focus {
  background-color: #2E4053;
}

/* favorite pages */
.content{
    max-width: 90rem;
    width: 100%;
    margin: 60px auto;
    height: 100vh;
    text-align: center;
}
.explore-restaurant{
  display: flex;
  justify-content: space-evenly;
  margin-top: 10vh;
}

#searchInput{
  padding: 14px 15px;
  font-family: Arial, Helvetica, sans-serif;
	font-size: 14px;
	font-weight: 400;
	border-radius: 5px;
	border: 1px solid #9ca3af;
}
.filter-wrapper{
  display: flex;
  align-items: center;
  gap: 5px;
}
.search-container{
  position: relative;
}
.search-container i{
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 1rem;
  color: #1b2631;
  border: 1px solid #e5e7eb;
  padding: 10px;
  cursor: pointer;
}

.restaurant-not-found{
  font-size: 1.5rem;
  font-weight: 300;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
/* skeleton */

.skeleton {
  user-select: none;
  background-color: #eeeeee;
  color: #eeeeee;
}

/* footer */

.footer-bar {
  background-color: #201f1f;
  padding : 15px 15px;
  font-display: optional;
}
.footer-bar p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  color: rgb(231, 185, 84);
  margin-bottom: 10px;
  margin-top: 10px;
  letter-spacing: 0.015em;
