t-size: 20px;\n  font-weight: 600;\n  text-decoration: none;\n  padding: 12px 0;\n}\nnav li:hover{\n  transform: scale(1.1);\n}\nnav li a:hover {\n  color: #eeeeee;\n}\n.logo{\n  display: flex;\n  text-decoration: none;\n}\n.logo-name{\n  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;\n  color: rgb(231, 185, 84);\n  font-weight: 600;\n  padding: 10px 0;\n  font-size: 2rem;\n}\n.logo:hover{\n  transform: scale(1.1);\n}\n.logo-name:hover{\n  color: #eeeeee;\n}\n.hamburger {\n  font-size: 3.2rem;\n  text-decoration: none;\n  color: rgb(231, 185, 84);\n  display: none;\n  margin-left: auto;\n}\nbutton{\n  border: none;\n  cursor: pointer;\n  background-color: transparent;\n}\n\n\n/* hero element */\n\n.hero-restaurant{\n  display: flex;\n  align-items: center;\n  text-align: center;\n  min-height: 500px;\n  width: 100%;\n  background-image:linear-gradient(rgba(78, 57, 57, 0.7),rgba(4,9,30,0.7)), url(\"../public/images/heros/hero-image_4-max.jpg\");\n  background-position: center;\n}\n.hero-desc-restaurant{\n  margin: 0 auto;\n  max-width: 850px;\n}\n.hero-title{\n  color: rgb(255, 253, 253);\n  font-size: 2rem;\n  padding: 10px;\n  font-weight: 700;\n  letter-spacing: 0.0155em;\n}\n.hero-desc{\n  color: rgb(233, 226, 226);\n  font-size: 1rem;\n  font-weight: 300;\n  line-height: 2.5;\n  padding: 3px 5px;\n}\n.hero-restaurant button{\n  font-size: 1.3rem;\n  border: 2px solid white;\n  color: white;\n  padding: 10px 15px;\n  width: 300px;\n  margin-top: 10px;\n  transition: all 0.15s ease-in-out;\n  border-radius: 7px;\n}\n.hero-restaurant button:hover{\n  background-color: #fff;\n  color: #000;\n}\n\n.hero-restaurant button:focus {\n  background-color: #fff;\n  color: #000;\n}\n\n\n/* Container Content */\n\n.content-restaurants{\n  width: 100%;\n  text-align: center;\n  margin: 60px auto;\n}\n.content-label{\n  text-align: center;\n  font-size: 3rem;\n  font-weight: bold;\n  color: rgb(172, 139, 68);\n  margin-bottom: 2vh;\n}\n\n/* list card */\n.lists{\n  display: grid;\n  /* grid-template-columns: repeat(auto-fit, minmax(280px, 1fr)); */\n  grid-template-columns: repeat(3, 1fr);\n  text-align: left;\n  gap: 25px;\n  align-items: start;\n  margin: 50px 50px;\n}\n#lists{\n  margin-top: 3vh !important;\n}\n\n.restaurants-item{\n  width: 100%;\n  border-radius: 7px;\n  overflow: hidden;\n  box-shadow: 0 4px 8px 0 rgba(17, 14, 14, 0.452);\n}\n\n\n/* Restaurant Card */\n\n.location{\n  font-size: 1.25rem;\n  padding: 10px 7px;\n  border-radius: 3px;\n  display: flex;\n  font-weight: bold;\n  margin-top: 15px;\n  position: absolute;\n  color: #fafafa;\n  z-index: 1;\n  background-color: rgb(231, 185, 84);\n}\n.restaurant-item_image{\n  object-fit: cover;\n  height: 200px;\n  z-index: 0;\n  width: 100%;\n}\n.restaurant-item_image:hover {\n  transform: scale(1.1);\n  transition: transform 0.7s ease-in-out;\n}\n.restaurant-item-description {\n  color: #141313;\n  max-height: 100px; \n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: normal;\n  display: -webkit-box;\n  -webkit-line-clamp: 4; \n  -webkit-box-orient: vertical;\n  font-size: 1.25rem;\n  margin: 10px 0;\n}\n.restaurant-item-date{\n  font-size: 1.5rem;\n  font-weight: 500;\n  letter-spacing: 0.025em;\n}\n\n.restaurant-item-date span{\n  font-size: 1.25rem;\n  font-weight: 400;\n}\n.restaurant-name{\n  margin-top: 15px;\n}\n.restaurant-name a{\n  text-decoration: none;\n  color: rgb(231, 185, 84);\n  font-size: 1.75rem;\n  padding: 10px 0;\n}\n.restaurant-name a:hover{\n  color: #141313;\n}\n.restaurant-item-content{\n  padding: 17px;\n}\n\n/* like button */\n/* .restaurant-img-detail .button {\n  width: 100%;\n} */\n\n.restaurant-img-detail .like-button {\n  display: flex;\n  justify-content: right;\n  align-items: right;\n  \n}\n\n.restaurant-img-detail .like {\n  position: absolute;\n  z-index: 4;\n  cursor: pointer;\n  outline: none;\n  border: none;\n  background-color: transparent;\n  margin-top: 8px;\n  margin-right: 10px;\n}\n\n.restaurant-img-detail .like:focus i {\n  border: 2px solid red;\n}\n\n.red {\n  color: red;\n}\n\n.restaurant-img-detail .like i {\n  font-size: 20px;\n  font-weight: bold;\n  background: rgba(255, 255, 255);\n  padding: 2px;\n  height: 45px;\n  width: 45px;\n  border-radius: 50%;\n  margin-top: 5px;\n  justify-content: center;\n  align-items: center;\n  display: flex;\n}\n\n/* detail card */\n\n.detail-container{\n  padding: 100px 120px 100px 100px;\n  margin-top: 40px;\n  max-width: 1300px;\n  width: 100%;\n  margin: auto;\n}\n.detail{\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  width: 100%;\n  margin-bottom: 30px;\n}\n.restaurant-img-detail{\n  \n  width: 100%;\n  max-height: 800px;\n}\n.restaurant-img-detail img{\n  width: 100%;\n  height: 100%;\n  object-fit: cover;\n  border-radius: 10px;\n  align-items: center;\n  position: relative;\n}\n.restaurant-detail {\n  padding: 15px 0;\n}\n.restaurant-detail .restaurant-name-detail{\n  font-size: 40px;\n  font-weight: bold;\n  padding: 15px 0px;\n  color: rgb(231, 185, 84);\n}\n.restaurant-detail p{\n  font-size: 24px;\n  padding: 5px 0;\n  font-weight: 700;\n}\n.restaurant-detail p span, .restaurant-detail span{\n  font-weight: 200;\n  font-size: 20px;\n  font-family: Arial, Helvetica, sans-serif;\n}\n.restaurant-desc .menu{\n  font-size: 1.5rem;\n  font-weight: bold;\n  padding: 15px 0px;\n}\n.restaurant-desc p{\n  font-weight: 200;\n  font-size: 1.4rem;\n  font-family: Arial, Helvetica, sans-serif;\n}\n\n /* detail review */\n .review {\n  font-family: Arial, Helvetica, sans-serif;\n  text-align: center;\n  margin-bottom: 30px;\n}\n\n.review-card {\n  margin-top: 3vh;\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 2vh;\n}\n\n.item-review {\n  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);\n  padding: 17px;\n  border-radius: 7px;\n  display: flex;\n  flex-direction: column;\n}\n\n.item-reviewer {\n  display: flex;\n}\n\n.reviewer-list {\n  text-align: left;\n  font-size: 1rem;\n  font-family: Arial, Helvetica, sans-serif;\n}\n\n.reviewer-list p:first-child {\n  font-weight: bold;\n  color: rgb(231, 185, 84);\n}\n\n.description-review {\n  font-size: 14px;\n  text-align: justify;\n  margin-top: 10px;\n  font-family: Arial, Helvetica, sans-serif;\n}\n\n.add-review {\n  display: flex;\n  flex-direction: column;\n  max-width: 100%;\n}\n.add-review h2{\n  margin: 20px 0;\n}\n.add-review h2, label{\n  font-family: Arial, Helvetica, sans-serif;\n}\n\n\n.add-review input,\ntextarea {\n  font-family: Arial, Helvetica, sans-serif;\n  width: 30%;\n  margin-top: 10px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  padding: 10px;\n  border-radius: 5px;\n  border: 1px solid #e5e5e5;\n  font-size: 14px;\n}\n\n.add-review input {\n  height: 50px;\n  margin-bottom: 24px;\n  max-width: 20%;\n}\n\n.add-review textarea {\n  max-width: 50%;\n}\n\n.add-review textarea:focus,\n.add-review input:focus {\n  outline: none;\n  border: 2px solid #717c86;\n}\n\n.add-review .submit {\n  width: 50%;\n  display: flex;\n  justify-content: left;\n}\n\n.submit button {\n  font-family: Arial, Helvetica, sans-serif;\n  font-size: 16px;\n  margin-top: 20px;\n  padding: 14px 30px;\n  border-radius: 5px;\n  border: none;\n  background-color: #85929E;\n  color: white;\n  font-weight: 600;\n  cursor: pointer;\n}\n\n.submit button:hover,\n.submit button:focus {\n  background-color: #2E4053;\n}\n\n/* favorite pages */\n.content{\n    max-width: 90rem;\n    width: 100%;\n    margin: 60px auto;\n    height: 100vh;\n    text-align: center;\n}\n.explore-restaurant{\n  display: flex;\n  justify-content: space-evenly;\n  margin-top: 10vh;\n}\n\n#searchInput{\n  padding: 14px 15px;\n  font-family: Arial, Helvetica, sans-serif;\n\tfont-size: 14px;\n\tfont-weight: 400;\n\tborder-radius: 5px;\n\tborder: 1px solid #9ca3af;\n}\n.filter-wrapper{\n  display: flex;\n  align-items: center;\n  gap: 5px;\n}\n.search-container{\n  position: relative;\n}\n.search-container i{\n  position: absolute;\n  top: 5px;\n  right: 5px;\n  font-size: 1rem;\n  color: #1b2631;\n  border: 1px solid #e5e7eb;\n  padding: 10px;\n  cursor: pointer;\n}\n\n.restaurant-not-found{\n  font-size: 1.5rem;\n  font-weight: 300;\n  text-align: center;\n  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;\n}\n/* skeleton */\n\n.skeleton {\n  user-select: none;\n  background-color: #eeeeee;\n  color: #eeeeee;\n}\n\n/* footer */\n\n.footer-bar {\n  background-color: #201f1f;\n  padding : 15px 15px;\n  font-display: optional;\n}\n.footer-bar p {\n  font-family: Arial, Helvetica, sans-serif;\n  font-size: 1rem;\n  font-weight: 600;\n  text-align: center;\n  color: rgb(231, 185, 84);\n  margin-bottom: 10px;\n  margin-top: 10px;\n  letter-spacing: 0.015em;\n}"],"sourceRoot":""}]);
